<template>
  <v-container id="dashboard">
    <v-expansion-panels flat v-model="panel" multiple>
      <v-expansion-panel v-model="panel">
        <v-expansion-panel-header> TASKS IN PROGRESS </v-expansion-panel-header>
        <v-expansion-panel-content>
          <v-list-item
            v-for="task in tasks.filter((task) => task.complete == false)"
            v-bind:key="task.id"
          >
            <v-list-item-avatar>
              <v-icon v-if="task.target_type == 'hashtag'"> mdi-pound </v-icon>
              <v-icon v-else-if="task.target_type == 'location'">
                mdi-map-marker
              </v-icon>
              <v-icon v-else-if="task.target_type == 'user'">
                mdi-account
              </v-icon>
            </v-list-item-avatar>

            <v-list-item-content>
              <v-list-item-title> {{ task.task_name }} </v-list-item-title>
              <v-list-item-subtitle>
                {{ task.targets.join(", ") }}
              </v-list-item-subtitle>
            </v-list-item-content>

            <v-list-item-action>
              <v-btn
                icon
                :to="{ name: 'view-task', params: { task_id: task.id } }"
              >
                <v-icon color="grey lighten-1">mdi-information</v-icon>
              </v-btn>
            </v-list-item-action>
          </v-list-item>
        </v-expansion-panel-content>
      </v-expansion-panel>

      <v-expansion-panel v-model="completedPanel">
        <v-expansion-panel-header> COMPLETED TASKS </v-expansion-panel-header>
        <v-expansion-panel-content>
          <v-list-item v-if="tasks.length === 0">No current tasks</v-list-item>
          <v-list-item
            v-for="task in tasks.filter((task) => task.complete)"
            v-bind:key="task.id"
          >
            <v-list-item-avatar>
              <v-icon v-if="task.target_type == 'hashtag'"> mdi-pound </v-icon>
              <v-icon v-else-if="task.target_type == 'location'">
                mdi-map-marker
              </v-icon>
              <v-icon v-else-if="task.target_type == 'user'">
                mdi-account
              </v-icon>
              <v-icon v-else> mdi-folder </v-icon>
            </v-list-item-avatar>

            <v-list-item-content>
              <v-list-item-title> {{ task.task_name }} </v-list-item-title>
              <v-list-item-subtitle>
                {{ task.targets.join(", ") }}
              </v-list-item-subtitle>
            </v-list-item-content>

            <v-list-item-action>
              <v-btn
                icon
                :to="{ name: 'view-task', params: { task_id: task.id } }"
              >
                <v-icon color="grey lighten-1">mdi-information</v-icon>
              </v-btn>
            </v-list-item-action>
          </v-list-item>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>

    <v-fab-transition>
      <v-btn fab large color="primary" fixed right bottom to="/new">
        <v-icon>mdi-plus-thick</v-icon></v-btn
      >
    </v-fab-transition>
  </v-container>
</template>

<script>
import { db } from "./db";

export default {
  name: "dashboard",
  data() {
    return {
      tasks: [],
      panel: [0],
    };
  },

  computed: {
    completed: function () {
      return this.tasks.filter(function (task) {
        return task.complete == true;
      });
    },
    not_completed: function () {
      return this.tasks.filter(function (task) {
        return task.complete == false;
      });
    },
  },

  firestore: {
    tasks: db.collection("tasks"),
  },
};
</script>