<template>
  <v-container id="new-task">
    <v-row cols="1">
      <v-col>
        <v-sheet rounded="lg" class="pa-4">
          <v-form ref="form" lazy-validation>
            <v-text-field
              v-model="task_name"
              label="Task Name"
              required
            ></v-text-field>

            <v-select
              v-model="target_type"
              :items="target_types"
              label="Target Type"
              required
            ></v-select>

            <v-text-field
              v-model="targets"
              label="Targets"
              required
            ></v-text-field>

            <v-select
              v-if="target_type == 'user'"
              v-model="fol_or_post"
              :items="fol_or_post_opt"
              label="What would you like to harvest?"
            ></v-select>

            <v-checkbox
              v-if="allowDateLimit"
              v-model="limitDates"
              label="Do you want to limit do a period?"
            ></v-checkbox>

            <v-menu
              v-if="limitDates"
              ref="menu"
              v-model="menu"
              :close-on-content-click="false"
              :return-value.sync="date"
              transition="scale-transition"
              offset-y
              min-width="auto"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  v-model="dateRangeText"
                  label="Date range"
                  prepend-icon="mdi-calendar"
                  readonly
                  v-bind="attrs"
                  v-on="on"
                ></v-text-field>
              </template>
              <v-date-picker v-model="dates" range no-title scrollable>
                <v-spacer></v-spacer>
                <v-btn text color="primary" @click="menu = false">
                  Cancel
                </v-btn>
                <v-btn text color="primary" @click="$refs.menu.save(dates)">
                  OK
                </v-btn>
              </v-date-picker>
            </v-menu>

            <v-card-actions>
              <v-btn to="/"> BACK </v-btn>
              <v-btn color="success" class="mr-4" @click="saveTask">
                Save Task
              </v-btn>
            </v-card-actions>
          </v-form>
        </v-sheet>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { db } from "./db";
import { uuid } from "vue-uuid";

export default {
  name: "new-task",
  data() {
    return {
      task_id: uuid.v4(),
      task_name: "",
      targets: "",
      target_type: "",
      fol_or_post_opt: ["followers", "posts"],
      fol_or_post: "posts",
      target_types: ["hashtag", "location", "user"],
      dates: [
        new Date().toISOString().split("T")[0],
        new Date().toISOString().split("T")[0],
      ],
      menu: false,
      limitDates: false,
    };
  },
  methods: {
    saveTask() {
      db.collection("tasks")
        .add({
          task_id: this.task_id,
          task_name: this.task_name,
          targets: this.targets.split(","),
          target_type: this.target_type,
          dates: this.dates,
          complete: false,
          limitDates: this.limitDates,
        })
        // eslint-disable-next-line no-unused-vars
        .then((docRef) => {
          this.$router.push("/");
        })
        .catch((err) => console.log(err));
    },
  },
  computed: {
    dateRangeText() {
      return this.dates.join(" ~ ");
    },
    getuuid() {
      return uuid.v1();
    },
    allowDateLimit() {
      if (["hashtag", "location"].includes(this.target_type)) {
        return true;
      } else if (this.target_type == "user") {
        if (this.fol_or_post == "posts") {
          return true;
        } else {
          return false;
        }
      } else {
        return false;
      }
    },
  },
};
</script>