<template>
  <v-container id="view-task">
    <v-row cols="1">
      <v-col>
        <v-sheet rounded="lg" class="pa-4">
          <v-toolbar flat>
            <v-card-title>{{ task.task_name }}</v-card-title>
            <v-chip label color="info">
              <v-icon left v-if="task.target_type == 'hashtag'">
                mdi-pound
              </v-icon>
              <v-icon left v-else-if="task.target_type == 'location'">
                mdi-map-marker
              </v-icon>
              <v-icon left v-else-if="task.target_type == 'user'">
                mdi-account
              </v-icon>
              {{ task.target_type }}
            </v-chip>
          </v-toolbar>

          <v-card-text>
            <v-chip class="mb-4" v-if="task.limitDates">
              <v-avatar>
                <v-icon>mdi-calendar</v-icon>
              </v-avatar>
              {{ task.dates[0] }} > {{ task.dates[1] }}
            </v-chip>

            <v-spacer></v-spacer>

            <v-chip
              class="ma-1"
              outlined
              v-for="target in task.targets"
              v-bind:key="target"
            >
              {{ target }}</v-chip
            >
          </v-card-text>
          <v-card-actions>
            <v-btn to="/"> BACK </v-btn>
            <v-btn color="error" @click="deleteTask"> DELETE </v-btn>
          </v-card-actions>
        </v-sheet>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { db } from "./db";

const tasks = db.collection("tasks");

export default {
  props: ["task_id"],
  name: "view-task",
  data() {
    return {
      task: null,
    };
  },

  watch: {
    task_id: {
      // call it upon creation too
      immediate: true,
      handler(task_id) {
        this.$bind("task", tasks.doc(task_id));
      },
    },
  },

  methods: {
    deleteTask() {
      if (confirm("Are you sure?")) {
        db.collection("tasks").doc(this.task_id).delete();
        this.$router.push("/");
      }
    },
  },
};
</script>