<template>
  <v-container id="login">
    <v-btn
      class="mt-md-12 mt-xs-auto"
      color="success"
      block
      x-large
      @click="googleSignIn"
      >Sign in with Backscatter account</v-btn
    >
  </v-container>
</template>

<script>
import firebase from "firebase";

export default {
  name: "Login",
  methods: {
    googleSignIn: function () {
      let provider = new firebase.auth.GoogleAuthProvider();
      firebase
        .auth()
        .signInWithPopup(provider)
        .then(() => {
          this.$router.push("/");
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>