import Vue from 'vue'
import App from './App.vue'
import router from './router'
import { firestorePlugin } from 'vuefire'
import vuetify from './plugins/vuetify';
import UUID from "vue-uuid";

Vue.use(firestorePlugin)
Vue.use(UUID);

Vue.config.productionTip = false

new Vue({
  router,
  vuetify,
  render: h => h(App)
}).$mount('#app')
