<template>
  <v-app>
    <v-app-bar app color="primary" flat dark>
      <v-row justify="center" align="center">
        <v-spacer></v-spacer>
        <v-toolbar-title
          color="white"
          style="cursor: pointer"
          @click="routeToMain"
        >
          <b>Scrape Controlla</b>
        </v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn v-if="checkLogin" text @click="logout">Log out</v-btn>
      </v-row>
    </v-app-bar>
    <v-main class="grey lighten-3"> <router-view /> </v-main>
  </v-app>
</template>

<script>
import firebase from "firebase";
import VueRouter from "vue-router";
const { isNavigationFailure, NavigationFailureType } = VueRouter;

export default {
  name: "App",
  methods: {
    logout: function () {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.push("/login");
        });
      this.isLoggedIn = false;
    },
    routeToMain: function () {
      this.$router.push("/").catch((e) => {
        if (!isNavigationFailure(e, NavigationFailureType.redirected)) {
          Promise.reject(e);
        }
      });
    },
  },
  computed: {
    checkLogin: function () {
      return this.$route.name != "login";
    },
  },
};
</script>
